  getTimePackages() {

    this.isLoading = true;

    this.dataService.getTimePackages()
      .subscribe((response: TimePackage[]) => {
        this.timePackagesList = response;
        this.dataService.cacheTimePackageSearch(this.timePackagesList);
        this.isLoading = false;
      },
        (err: any) => console.log(err),
        () => console.log('getTimePackages() retrieved time packages'));

    
    console.log(this.timePackagesList);
  }


  ngOnInit(): void {
    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/tariffs/timepackage-search";   
    this.getTimePackages();

    this.dataService.menuUnselect();
    $("#menuTimeTariffs").parent().parent().addClass("open");
    $("#menuTimeTariffs").parent().show();
    $("#menuTimeTariffs").addClass("active");
  }

  editTimePackage(timePackage: TimePackage) {
    this.dataService.setSelectedTimePackage(timePackage);
  }

  addTimePackage() {

    var newTimePackage = new TimePackage();
    newTimePackage.id = 0;

    this.dataService.setSelectedTimePackage(newTimePackage);
  }

