import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { PasswordModule } from 'primeng/primeng';
import { TimePackageSearchComponent } from './timepackage.search.component';
import { AuthGuard } from '../guards/auth.guard';
import { AdminGuard } from '../guards/admin.guard';
import { SiteLayoutComponent } from '../layout/site-layout.component';
import { TariffsService } from './tariffs.service';
import { TimePackageComponent } from './timepackage.component';
import { ZonePackageComponent } from './zonepackage.component';
import { TimeBandComponent } from './timeband.component';
import { CalendarModule } from 'primeng/primeng';
import { ZonePackageSearchComponent } from './zonepackage.search.component';
import { ZoneComponent } from './zone.component';
import { FilterPipe } from './filter.pipe';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';


@NgModule({
    imports: [

        BrowserModule,
        HttpModule,
        CommonModule,
        FormsModule,
        CalendarModule,
        NgbModule,
        PasswordModule,
        RouterModule.forRoot([
            {
                path: 'tariffs',
                component: SiteLayoutComponent,
                children: [
                    { path: 'timepackage-search', component: TimePackageSearchComponent, canActivate: [AdminGuard] },
                    { path: 'zonepackage-search', component: ZonePackageSearchComponent, canActivate: [AdminGuard] },
                    { path: 'timepackage/:id', component: TimePackageComponent, canActivate: [AdminGuard] },
                    { path: 'timepackage', component: TimePackageComponent, canActivate: [AuthGuard] },
                    { path: 'zonepackage/:id', component: ZonePackageComponent, canActivate: [AdminGuard] },
                    { path: 'zonepackage', component: ZonePackageComponent, canActivate: [AuthGuard] },
                    { path: 'timeband/:id', component: TimeBandComponent, canActivate: [AdminGuard] },
                    { path: 'timeband', component: TimeBandComponent, canActivate: [AuthGuard] },
                    { path: 'zone/:id', component: ZoneComponent, canActivate: [AdminGuard] },
                    { path: 'zone', component: ZoneComponent, canActivate: [AuthGuard] },
                ]
            }
        ])
    ],
    declarations: [
        TimePackageSearchComponent,
        TimePackageComponent,
        TimeBandComponent,
        ZonePackageSearchComponent,
        ZonePackageComponent,
        ZoneComponent,
        FilterPipe

    ],
    providers: [
        TariffsService
    ]
})
export class TariffsModule { }
