import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { ZonePackage } from './zonepackage';
import { DataService } from '../data.service';
import { AuthenticationService } from '../services/index';
import { Zone } from './zone';
declare var $: any;

@Component({
  templateUrl: 'zonepackage.component.html'
})
export class ZonePackageComponent implements OnInit {

  pageTitle: string = 'Zone Package Detail';
  
  public zonesList: Zone[] = [];
  isZonesLoading: boolean = false;
  zonepackage: ZonePackage = new ZonePackage();

  currentProgress: number = 0;

  constructor(private dataService: DataService,
    private authenticationService: AuthenticationService,
    public toastr: ToastrService, vcr: ViewContainerRef,
    private router: Router) {
    //this.toastr.setRootViewContainerRef(vcr);
  }


  cancel(): void {

    this.dataService.setSelectedZonePackage(new ZonePackage());

    this.router.navigateByUrl(this.dataService.previousPageRoute$);   
  }
 
 
    saveZonePackage(form): void {
        var isValid = true;        
        this.zonepackage.createdByUserId = this.authenticationService.loggedUser$.id;
          
        if (this.zonepackage.id == 0 || this.zonepackage.id == undefined || this.zonepackage.id == null) {
          
            this.zonepackage.id = 0;               
        }
        else {
            isValid = false
        }

        if (isValid) {           
            this.dataService.saveZonePackage(this.zonepackage)
                .subscribe((zonepackage: ZonePackage) => {
                    this.zonepackage = zonepackage;
                        this.toastr.success('Creation Successfully Completed!', 'Success!');
                    this.dataService.setSelectedZonePackage(this.zonepackage);
                    },
                        (err: any) => this.toastr.error(err.json().message));
            }
        
    }

  ngOnInit(): void {

    this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
    this.dataService.currentPageRoute$ = "/tariffs/zonepackage";
    this.zonepackage = this.dataService.getSelectedZonePackage();
    console.log(this.zonepackage);
    if (this.dataService.getSelectedZonePackage().id > 0) {
     
      this.isZonesLoading = true;
      this.dataService.getZones(this.dataService.getSelectedZonePackage().id)
        .subscribe((response: Zone[]) => {
          this.isZonesLoading = false;
          
          this.zonesList = response;
          this.dataService.cacheZoneSearch(this.zonesList);
            console.log(this.zonesList);

        },
        (err: any) => console.log(err),
        () => console.log('getZones retrieved zones'));

    }
    else {
      this.clearZonePackage(null);
    }  
    this.dataService.menuUnselect();
   
  }

  clearZonePackage(form) {
      this.zonepackage = new ZonePackage();
      this.zonepackage.id = 0;
    //this.customer.status = "Active";
    this.dataService.setSelectedZonePackage(new ZonePackage());

    if (form != null)
      form.reset();
  }

  calcZonePackageProgress(): number {

    var current: number = 0;

    if (!this.checkIsEmpty(this.zonepackage.name))
      current += 100;
   
    if (current > 100)
          current = 100;

    this.currentProgress = current;
    return current;
  }

  checkIsEmpty(value: any): boolean {

    if (value == "" || value == undefined || value == null)
      return true;

    return false;
  }

  editZone(zone: Zone) {
      this.dataService.setSelectedZone(zone);
    }
    addZone() {

        var newZone = new Zone();
        newZone.id = 0;
        newZone.zonepackageId = this.dataService.getSelectedZonePackage().id;

        this.dataService.setSelectedZone(newZone);
    }
}

