import { Component, OnInit, OnDestroy, Input, Output } from '@angular/core';
import { ViewContainerRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
//import { ToastsManager, } from 'ng2-toastr/ng2-toastr';
import { ToastrService } from 'ngx-toastr';
import { DataService } from '../data.service';
import { AuthenticationService } from '../services/index';
import { TimeBand } from './timeband';
import { TimeInterval } from './timeinterval';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { TimeModel } from './timeModel';
declare var $: any;

@Component({
    templateUrl: 'timeband.component.html'
})
export class TimeBandComponent implements OnInit {

    pageTitle: string = 'Time Band Detail';

    public timeIntervalsList: TimeInterval[] = [];
    timeinterval: TimeInterval = new TimeInterval();
    isTimeIntervalsLoading: boolean = false;
    timeband: TimeBand = new TimeBand();
    startTime: TimeModel;
    endTime: TimeModel;

    currentProgress: number = 0;

    constructor(private dataService: DataService,
        private authenticationService: AuthenticationService,
        public toastr: ToastrService, vcr: ViewContainerRef,
        private router: Router) {
        //this.toastr.setRootViewContainerRef(vcr);
    }

    cancel(): void {

        this.dataService.setSelectedTimeBand(new TimeBand());

        this.router.navigateByUrl(this.dataService.previousPageRoute$);
    }

    saveTimeBand(form): void {
        var isValid = true;

        this.timeband.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.timeinterval.id == 0 || this.timeinterval.id == undefined || this.timeinterval.id == null) {

            this.timeinterval.id = 0;
            isValid = false
        }

        if (isValid) {
            console.log(this.timeband);
            this.dataService.saveTimeBand(this.timeband)
                .subscribe((timeband: TimeBand) => {

                    this.timeband = timeband;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.dataService.setSelectedTimeBand(this.timeband);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
    }

    saveTimeInterval(form): void {

        var isValid = true;

        this.timeinterval.createdByUserId = this.authenticationService.loggedUser$.id;

        if (this.timeband.id == 0 || this.timeband.id == undefined || this.timeband.id == null) {

            this.timeinterval.timebandId = 0;
            isValid = false
        }

        if (isValid) {
            console.log(this.timeinterval);
            this.dataService.saveTimeInterval(this.timeinterval)
                .subscribe((timeinterval: TimeInterval) => {
                    console.log(timeinterval);
                    this.timeinterval = timeinterval;
                    this.toastr.success('Creation Successfully Completed!', 'Success!');

                    this.loadTimeIntervals(this.dataService.getSelectedTimeBand().id);
                    $('#responsive').modal('toggle');
                    this.dataService.setSelectedTimeInterval(this.timeinterval);
                },
                    (err: any) => this.toastr.error(err.json().message));
        }
    }

    ngOnInit(): void {
        this.startTime = new TimeModel();
        this.startTime.hour = 12;
        this.startTime.minute = 20;
        this.endTime = new TimeModel();
        this.endTime.hour = 13;
        this.endTime.minute = 20;

        this.dataService.previousPageRoute$ = this.dataService.currentPageRoute$;
        this.dataService.currentPageRoute$ = "/tariffs/timeband";
        this.timeband = this.dataService.getSelectedTimeBand();
        this.timeinterval = this.dataService.getSelectedTimeInterval();
        console.log(this.timeband);

        if (this.dataService.getSelectedTimeBand().id > 0) {

            this.isTimeIntervalsLoading = true;

            this.loadTimeIntervals(this.dataService.getSelectedTimeBand().id);

            

        }
        else {
            this.clearTimeBand(null);
        }

        this.dataService.menuUnselect();

    }

    loadTimeIntervals(id) {
        this.dataService.getTimeIntervals(id)
            .subscribe((response: TimeInterval[]) => {
                this.isTimeIntervalsLoading = false;
                this.timeIntervalsList = response;
                console.log(this.timeIntervalsList);

            },
                (err: any) => console.log(err),
                () => console.log('getTimeIntervals retrieved time intervals'));
    }

    clearTimeBand(form) {
        this.timeband = new TimeBand();
        this.timeband.id = 0;
        this.timeband.timepackageId = this.dataService.getSelectedTimePackage().id;
        this.timeinterval.timebandId = this.dataService.getSelectedTimeBand().id;
        this.dataService.setSelectedTimeBand(new TimeBand());


        if (form != null)
            form.reset();
    }

    calcTimeBandProgress(): number {

        var current: number = 0;

        if (!this.checkIsEmpty(this.timeband.name))
            current += 100;

        if (current > 100)
            current = 100;

        this.currentProgress = current;

        return current;
    }

    checkIsEmpty(value: any): boolean {

        if (value == "" || value == undefined || value == null)
            return true;

        return false;
    }

    editTimeInterval(timeinterval: TimeInterval) {
        console.log(timeinterval);

        

        this.dataService.setSelectedTimeInterval(timeinterval);
        //this.timeinterval = timeinterval;
        this.timeinterval = { ...timeinterval }
    }
    addTimeInterval() {
        this.timeinterval = new TimeInterval();
        this.timeinterval.id = 0;
        this.timeinterval.timebandId = this.dataService.getSelectedTimeBand().id;

        this.dataService.setSelectedTimeInterval(this.timeinterval);
    }
}

