import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/throw';
import { TimePackage } from './timepackage';

@Injectable()
export class TariffsService {
  private _customerUrl = 'api/tariffs/tariffs.json';

  constructor(private _http: Http) { }

  getTimePackages(): Observable<TimePackage[]> {
    return this._http.get(this._customerUrl)
      .map((response: Response) => <TimePackage[]>response.json())
      .do(data => console.log('All: ' + JSON.stringify(data)))
      .catch(this.handleError);
  }

  getTimePackage(id: number): Observable<TimePackage> {
    return this.getTimePackages()
      .map((timepackages: TimePackage[]) => timepackages.find(p => p.id === id));
  }

  private handleError(error: Response) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    console.error(error);
    return Observable.throw(error.json().error || 'Server error');
  }
}
